/**
 * fspec TUI Demo Script
 * This demo uses @microsoft/tui-test to run the actual fspec command
 */

import { test, expect } from '@microsoft/tui-test';
import { join } from 'path';
import { homedir } from 'os';

const fspecPath = join(homedir(), 'projects', 'fspec', 'dist', 'index.js');

test.use({
  program: {
    file: process.execPath, // node
    args: [fspecPath]
  }
});

test('fspec demo recording', async ({ terminal }) => {
  // Wait for fspec TUI to load
  await expect(terminal.getByText('fspec', { full: true })).toBeVisible({ timeout: 5000 });

  // Let it display for 3 seconds
  await new Promise(resolve => setTimeout(resolve, 3000));

  // Quit fspec with ESC key
  terminal.keyEscape();

  // Wait a moment for graceful exit
  await new Promise(resolve => setTimeout(resolve, 500));
});
